body {
  font-family:'Times New Roman', Times, serif;
  margin: 0;
  padding: 0;
  /* background-color: #c4c4c7; */
}

.app-container {
  display: flex;
  flex-direction: column;
}

.header {
  background-image: url('./assets/ladychildrenlearning.jpg');
  background-size: cover; 
  background-position: center; 
  background-repeat: no-repeat; 
  background-color: #3f51b5; 
  color: white; 
  padding: 20px; 
  text-align: center;
  height: 250px; 
}

.header, h1{
  font-size: xx-large;
  font-weight: bolder;
  margin-top: 1cm;
  font-style: normal;
}

.header, p{
  font-size:medium;
  font-weight: bolder;
  font-style: italic;
}


.nav1, .nav2 {
  background: #f5f5f5;
  padding: 10px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  text-align: center;
}

.nav2{
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.parentsnav{
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  margin-right: 10px;
  padding-right: 10px;
  padding-left: 10px;
  border-style: groove;
  border-color: #a47103;
  border-radius: 25px;
}

.nav-link {
  margin: 0 20px;
  color: #3f51b5;
  text-decoration: none;
}

.upcoming-kids-event{
  color: #a47103;
  font-size: large;
}

.searchbtn{
	font-size: xx-large;
  font-weight: 700;
  font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.ontopnav {
  margin-bottom: 20px;
}

.main-content {
  padding: 20px;
}

.signup-parent, .signup-tutor, .parentslogin {
  max-width: 300px;
  margin: 20px auto;
  padding: 20px;
  border: 1px solid #ddd;
  background-color: white;
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.search-tutors{
  max-width: 300px;
  margin: 20px auto;
  padding: 20px;
  
  border-radius: 300px;
  /* border: 2px solid #a47103; */
  background-color: rgba(242, 241, 245, 0.628);
  border-radius: 5px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
}

.signup-parent form,
.signup-tutor form {
  display: flex;
  flex-direction: column;
}

.signup-parent input,
.signup-tutor input {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ddd;
  border-radius: 5px;
}

button {
  padding: 10px;
  background-color: #3f51b5;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #5c6bc0;
}

.tutor-list {
  list-style-type: none;
  padding: 0;
}

.tutor-list li {
  border: 1px solid #ddd;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 5px;
  display: flex;
  align-items: center;
}

.tutor-list img {
  border-radius: 50%;
  margin-right: 20px;
}

.back-button {
  margin-bottom: 20px;
}

